import {Component, OnDestroy, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {ApiService} from '../../api.service';
import {SharedServiceService} from '../../shared-service.service';
import {StorageService} from '../../shared-components/storage.service';
import {appConstants} from '../../../assets/constants/app-constants';

@Component({
    selector: 'services',
    templateUrl: './services.page.html',
    styleUrls: ['./services.page.scss'],
})
export class ServicesPage {

    constructor(private storageService: StorageService, private sharedService: SharedServiceService, private router: Router, private adminService: ApiService) {
    }

    amountPurchased = 0;
    selectedServices = [];
    isRefreshed = false;
    refreshRate: any = Math.random();

    ionViewWillEnter() {
        this.sharedService.showSearchBox.next(true);
        this.refreshRate = Math.random();
    }

    ionViewWillLeave() {
        this.sharedService.showSearchBox.next(false);
    }

    refreshPage(event) {
        setTimeout(() => {
            this.isRefreshed = !this.isRefreshed;
            this.amountPurchased = 0;
            this.selectedServices = [];
            event.target.complete();
        }, 1500);
    }

    onBookAppointment() {
        this.storageService.getStoredValue(appConstants.SELECTED_SERVICES).then((services) => {
            this.sharedService.updateCart.next(services);
            this.router.navigate(['/schedule-appointment']);
        });
    }

    updateData(data) {
        this.storageService.storeValue(appConstants.SELECTED_SERVICES, data.selectedServices);
        this.selectedServices = data.selectedServices;
        this.amountPurchased = data.amountPurchased;
    }
}
